namespace $ { export class $mol_floater extends $mol_viewer {

	/// shiftStyle \
	shiftStyle() {
		return ""
	}

	/// field * style.transform < shiftStyle
	field() {
		return $mol_merge_dict( super.field() , {
			"style.transform" : ()=> <any> this.shiftStyle() ,
		} )
	}

	/// scrolling false
	scrolling() {
		return false
	}

	/// attr * mol_floater_scrolling < scrolling
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_floater_scrolling" : ()=> <any> this.scrolling() ,
		} )
	}

} }

