namespace $ { export class $mol_svg extends $mol_viewer {

	/// tagName \svg
	tagName() {
		return "svg"
	}

	/// nameSpace \http://www.w3.org/2000/svg
	nameSpace() {
		return "http://www.w3.org/2000/svg"
	}

} }

namespace $ { export class $mol_svg_root extends $mol_viewer {

	/// tagName \svg
	tagName() {
		return "svg"
	}

	/// viewBox \0 0 100 100
	viewBox() {
		return "0 0 100 100 "
	}

	/// attr * viewBox < viewBox
	attr() {
		return $mol_merge_dict( super.attr() , {
			"viewBox" : ()=> <any> this.viewBox() ,
		} )
	}

} }

namespace $ { export class $mol_svg_path extends $mol_svg {

	/// tagName \path
	tagName() {
		return "path"
	}

	/// geometry \
	geometry() {
		return ""
	}

	/// attr * d < geometry
	attr() {
		return $mol_merge_dict( super.attr() , {
			"d" : ()=> <any> this.geometry() ,
		} )
	}

} }

namespace $ { export class $mol_svg_circle extends $mol_svg {

	/// tagName \circle
	tagName() {
		return "circle"
	}

	/// radius \
	radius() {
		return ""
	}

	/// posX \
	posX() {
		return ""
	}

	/// posY \
	posY() {
		return ""
	}

	/// attr * 
	/// 	r < radius 
	/// 	cx < posX 
	/// 	cy < posY
	attr() {
		return $mol_merge_dict( super.attr() , {
			"r" : ()=> <any> this.radius() ,
			"cx" : ()=> <any> this.posX() ,
			"cy" : ()=> <any> this.posY() ,
		} )
	}

} }

