namespace $ { export class $mol_app_habhub extends $mol_pager {

	/// title \HabHub
	title() {
		return "HabHub"
	}

	/// gisters /
	gisters() {
		return [] as any[]
	}

	/// statuser $mol_statuser status < gisters
	@ $mol_mem()
	statuser( next? : any ) {
		return new $mol_statuser().setup( obj => { 
			obj.status = () => this.gisters()
		} )
	}

	/// lister $mol_lister rows < gisters
	@ $mol_mem()
	lister( next? : any ) {
		return new $mol_lister().setup( obj => { 
			obj.rows = () => this.gisters()
		} )
	}

	/// body / 
	/// 	< statuser 
	/// 	< lister
	body() {
		return [].concat( this.statuser() , this.lister() )
	}

	/// gistContent# \
	gistContent( key : any ) {
		return ""
	}

	/// gister# $mol_texter text < gistContent#
	@ $mol_mem_key()
	gister( key : any , next? : any ) {
		return new $mol_texter().setup( obj => { 
			obj.text = () => this.gistContent( key )
		} )
	}

	/// footer null
	footer() {
		return <any> null
	}

} }

